.btn {
    width: 100%;
    background-color: #008cff;
    border: 1px solid #4a7fab;
    border-radius: 17px;
    padding: 20px 0;
    color: #ffffff;
    font-size: 22px;
    font-weight: 700;
    margin: 30px 0;
}

.btn:hover {
    cursor: pointer;
}

.btn:focus {
    outline: none;
}